import React, { Component } from 'react';
import { ListGroup, ListGroupItem, CardBody, Row, Col, TabContent, TabPane } from 'reactstrap';
import { Redirect } from 'react-router';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import EntityActions from '../Redux/EntityRedux';
import BusinessCenterActions from '../Redux/BusinessCenterRedux';
import TransactionTableContainer from './TransactionTableContainer';
import TransactionActions from '../Redux/TransactionRedux';

class LocalTransactionContainer extends Component {
    state = {
        entity:{
            id: localStorage.getItem('entity_selected_id'),
            name: localStorage.getItem('entity_selected_name'),
        },
        account:{
            id: localStorage.getItem('account_selected_id'),
        },
    }
    componentDidMount = () => {
        this.props.actions.business_center.fetchEntityBusinessCenters(this.state.entity.id);
    }
    onSearchTransactions = (startDt, endDt, businessCenterID) => {
        this.props.actions.transaction.getAccountTransactions(
            this.state.account.id,
            {
                start_dt: startDt,
                end_dt: endDt,
                is_local: 1,
                business_center_id: businessCenterID,
           },
            ['account_origin', 'account_destine', 'business_center', 'assets']
        );
    }
    onDeleteAsset = (transactionId, assetId) => {
        this.props.actions.transaction.deleteTransactionAsset(transactionId, assetId);
    }

    onDeleteTransaction = (transactionId) => {
        this.props.actions.transaction.deleteTransaction(transactionId);
    }

    render() {
        const options = {
            showMovementCode: false,
            showBalance: false,
            showAccount: true,
            title: 'Cartola local',
        };
        return (
            <CardBody>
                <Row>
                    <Col xs="12" sm="12">
                        <TransactionTableContainer
                            entity={this.state.entity}
                            transactions={this.props.transaction.transactions}
                            business_centers={this.props.business_center.business_centers}
                            searchTransactions={this.onSearchTransactions}
                            deleteTransactionAsset={this.onDeleteAsset}
                            updateTransaction={this.props.actions.transaction.updateTransaction}
                            options={options}
                        />
                    </Col>
                </Row>
            </CardBody>
        );
    }
}

const mapStateToProps = (state) => ({
    transaction : state.transaction,
    entity : state.entity,
    business_center : state.business_center,
});

const mapDispatchToProps = (dispatch) => ({
	actions: {
        transaction: bindActionCreators(TransactionActions, dispatch),
        business_center: bindActionCreators(BusinessCenterActions, dispatch),
    },
});

export default connect(mapStateToProps, mapDispatchToProps)(LocalTransactionContainer);


