import React, {Component} from 'react';
import {
    ListGroup,
    ListGroupItem,
    Card,
    CardBody,
    CardHeader,
    Row,
    Col,
    Table,
    Badge,
    Label,
    Input,
    FormGroup,
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Collapse,
    ButtonGroup
} from 'reactstrap';
import {Redirect} from 'react-router';
import moment from 'moment';
import 'react-widgets/dist/css/react-widgets.css';
import DateTimePicker from 'react-widgets/lib/DateTimePicker';
import momentLocalizer from "react-widgets-moment";
import util, {IMAGE} from '../../src/util';

momentLocalizer(moment);

export default class TransactionTableContainer extends Component {
    inputFile = null;
    constructor(props, context) {
        super(props, context);
    }
    state = {
        startDt: moment().startOf('month').format('YYYY-MM-DD').toString(),
        endDt: moment().endOf('month').format('YYYY-MM-DD').toString(),
        business_center_id: null,
        redirectoToDetail: false,
        transaction_id: null,
        uploadAsset:{
            open:false,
            data:null,
            name:null,
            type:null,
            description:null,
        },
        detailAsset:{
            open:false,
            name:null,
            type:null,
            url:null,
            description:null,
            transactionId: null,
        },
        width: window.innerWidth,
        mobileFiltersOpen:false,
        modalLinkTransactions:{
            open: false
        },
        modalDocuments:{
            open:false,
            transaction: null,
        }
    }
    componentWillMount() {
        window.addEventListener('resize', this.handleWindowSizeChange);
    }
    componentDidMount() {
        this.props.searchTransactions(this.state.startDt, this.state.endDt, this.state.business_center_id);
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleWindowSizeChange);
    }
    handleWindowSizeChange = () => {
        this.setState({ width: window.innerWidth });
    };
    changeStartDt = (date) => {
        this.setState({startDt: moment(date).format('YYYY-MM-DD').toString()});
    }
    changeEndDt = (date) => {
        this.setState({endDt: moment(date).format('YYYY-MM-DD').toString()});
    }
    onchangeBusinessCenter = (transactionId, bcId) => {
        this.props.updateTransaction(transactionId, {business_center_id: bcId, assets:[]});
    }
    onFilterBusinessCenter = (bcId) => {
        this.setState({business_center_id: bcId});
    }
    renderBusinessCenterDropDown = (transaction) => {
        let comp = null;
        let bcId = transaction.business_center_id;
        if (transaction.is_protected === true){
            comp = <input type="text" readOnly value={transaction.business_center.name} />
        }
        else{
            comp = <Input
                        onChange={(event) => {
                            this.onchangeBusinessCenter(transaction.id, event.target.value)
                       }}
                        type="select"
                        name="select"
                        key={'bc-' + bcId}
                    >
                    {this.props.business_centers.map(bc => {
                        return <option selected={bc.id === bcId} key={bc.id} value={bc.id}>{bc.name}</option>
                    })}
                </Input>;
       }

        return comp;
    }
    onShowLocalTransactions = (event) => {
        this.setState({is_local: event.target.checked ? 1 : 0});
    }
    onUploadClick = (transaction_id) =>{
        this.setState({transaction_id});
        this.inputFile.click();
    }
    handleUpload = (output) => {
        if (!output.error){
            this.setState({
                uploadAsset: {
                    open: true,
                    data: output.data,
                    name: output.name,
                    type: output.type,
                }
            });
        }
        else{
            this.setState({modal: {
                open: true,
                title:'',
                description:'',
            }});
        }
    }
    openAsset = (transactionId, asset) => {
        this.setState({
            detailAsset:{
                ...asset,
                transactionId: transactionId,
                open:true,
            }
        });
    }
    renderDocumentColumn = (transaction) => {
        return (
            <div>
                <input
                    style={{display:'none'}}
                    hidden
                    ref={input => this.inputFile = input}
                    type="file"
                    onChange={e => util.onFileChange(e.target.files, this.handleUpload)}
                />
                <ButtonGroup>
                    {transaction.assets.length > 0 ? <Button onClick={() => this.toggleDocumentsModel(transaction)} color="success"><i className="fa fa-file"></i> ({transaction.assets.length}) </Button> : null}
                    <Button color="primary" onClick={() => {this.onUploadClick(transaction.id)}}><i className="fa fa-cloud-upload"></i> Subir</Button>
                </ButtonGroup>
            </div>
        );
    }
    toggleModal = () => {
        this.setState({
            uploadAsset:{
                open: !this.state.uploadAsset.open
            }
        });
    }
    toggleDetailModal = () => {
        this.setState({
            detailAsset:{
                open: !this.state.detailAsset.open
            }
        });
    }
    toggleLinkTransactionsModal = () => {
        this.setState({
            modalLinkTransactions:{
                open: !this.state.modalLinkTransactions.open
            }
        });
    }
    onChangeAssetDescription = (e) => {
        let prevState = this.state.uploadAsset;
        this.setState({uploadAsset:{...prevState, name: e.target.value}})
    }
    uploadAsset = () => {
        const tid = this.state.transaction_id;
        this.setState({
            uploadAsset: {open:false},
            transaction_id: null,
        });

        this.props.updateTransaction(tid, {}, [{
            data: this.state.uploadAsset.data,
            name: this.state.uploadAsset.name,
            type: this.state.uploadAsset.type,
        }]);

        this.props.searchTransactions(this.state.startDt, this.state.endDt, this.state.business_center_id);
    }
    deleteAsset = (transactionId, assetId) => {
        this.props.deleteTransactionAsset(this.state.detailAsset.transactionId ,this.state.detailAsset.id);
        this.props.searchTransactions(this.state.startDt, this.state.endDt, this.state.business_center_id);
        this.setState({detailAsset:{open: false}});
    }
    getIconClass = (extension) => {
        let icon = 'fa fa-lg fa-file';
        if (extension === 'png' || extension === 'jpg' || extension === 'jpeg' || extension === 'gif'){
            icon = "fa fa-lg fa-image";
        }
        else if (extension === 'pdf'){
            icon = "fa fa-lg fa-file-pdf-o";
        }
        else if (extension === 'doc' || extension === 'docx' || extension === 'txt'){
            icon = "fa fa-lg fa-file-text-o";
        }
        else if (extension === 'xls' || extension === 'xlsx'){
            icon = "fa fa-lg fa-file-excel-o";
        }
        console.log({icon, extension})
        return icon;
    }
    renderCardDocuments = (transaction) => {
        if (transaction && transaction.assets && transaction.assets.length > 0){
            return <ListGroup>
                    {
                        transaction.assets.map((asset, index) => {
                            return <ListGroupItem key={asset.id}>
                                        <Row>
                                            <Col xs="2"><i className={this.getIconClass(asset.type)}></i> </Col>
                                            <Col xs="10">
                                                <Row>
                                                    {
                                                        IMAGE.indexOf(this.state.detailAsset.type) >= 0 ?
                                                        <a href={asset.url} target='_blank'>{asset.name}<br/><img src={asset.url} height="200"/></a>
                                                        :
                                                        <a href={asset.url} target='_blank'>{asset.name}</a>
                                                    }
                                                </Row>
                                                <Row><small><strong>F. Creacin:</strong> {asset.created_dt}</small></Row>
                                            </Col>
                                        </Row>
                                    </ListGroupItem>
                        })
                    }
                </ListGroup>
        }
        else{
            return null;
        }
    }
    renderTitle = () => {
        return <CardHeader>
                    <i className="fa fa-align-justify"></i><strong>{this.props.entity.name}</strong> {this.props.options.title}
                </CardHeader>
    }
    toggleDocumentsModel = (transaction) => {
        this.setState({
            modalDocuments:{
                open:!this.state.modalDocuments.open,
                transaction
            }
        });
    }
    renderDocumentsModal = () => {
        const transaction = this.state.modalDocuments.transaction;
        if (transaction){
            return <Modal isOpen={this.state.modalDocuments.open} toggle={() => this.toggleDocumentsModel(null)} className={'modal-lg modal-success'}>
                <ModalHeader toggle={() => this.toggleDocumentsModel(null)}>Documentos</ModalHeader>
                <ModalBody>
                    <Col md="12" xs="12" style={{marginBottom:20}}>
                        <Row><label>{transaction.description} <strong>{this.renderTransactionAmount(transaction.amount)}</strong></label></Row>
                        <Row><small>F. Contable: {transaction.date}</small></Row>
                        <Row><small>F. Descarga: {transaction.created_dt}</small></Row>
                    </Col>
                    {this.renderCardDocuments(transaction)}
                </ModalBody>
                <ModalFooter>
                    <Button color="success" onClick={() => this.toggleDocumentsModel(null)}>Ok</Button>{' '}
                </ModalFooter>
            </Modal>
        }
        else{
            return null;
        }
    }
    renderTransactionAmount = (amount) => {
        return <label style={{fontWeight:'bold', color: amount < 0 ? '#e83e8c' : '#20a8d8' }}>{util.formatMoney(amount)}</label>
    }
    renderDesktop = () => {
        return (
            <Card>
                {this.renderTitle()}
                <CardBody>
                    <Row>
                        <Col xs="6">
                            <Label>Fecha de inicio</Label><DateTimePicker onChange={this.changeStartDt}/>
                        </Col>
                        <Col xs="6">
                            <Label>Fecha de fin</Label><DateTimePicker onChange={this.changeEndDt}/>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs="6">
                            <Label>Categora</Label>
                            <Input
                                onChange={(event) => {this.onFilterBusinessCenter(event.target.value)}}
                                type="select"
                                name="select">
                                    <option key={'no-option'}>Seleccione una categora</option>
                                {this.props.business_centers.map(bc => {
                                    return <option key={bc.id} value={bc.id}>{bc.name}</option>
                            })}
                            </Input>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs="12">
                            <button
                                onClick={() => this.props.searchTransactions(this.state.startDt, this.state.endDt, this.state.business_center_id)}
                                className="btn btn-primary pull-right">
                                Filtrar
                            </button>
                        </Col>
                    </Row>
                    <Row><hr /></Row>
                </CardBody>
                <CardBody>
                    <Table responsive hover>
                        <thead>
                            <tr>
                                <th>ID</th>
                                {this.props.options.showAccount ? <th>Cuenta</th> : null}
                                <th>Descripcin</th>
                                <th>Monto</th>
                                {this.props.options.showBalance ? <th>Saldo</th> : null}
                                <th>Categora</th>
                                {this.props.options.showMovementCode ? <th>Banco ID</th> : null}
                                <th>Documentos</th>
                                <th>F.Contable</th>
                                <th>F.Creacin</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.props.transactions.map((transaction, index) => {
                                    return <tr key={transaction.id}>
                                                <td style={{ width:30 }}>{transaction.id}</td>
                                                {this.props.options.showAccount ? <td style={{ width:100 }}>{transaction.account_destine.name}</td> : null}
                                                <td style={{ width:200 }}>{transaction.description} {transaction.is_local ? <Badge color="warning">Local</Badge> : null} </td>
                                                <td style={{ width:50 }}>{this.renderTransactionAmount(transaction.amount)}</td>
                                                {this.props.options.showBalance ? <td style={{ width:50 }}>{util.formatMoney(transaction.balance)}</td> : null}
                                                <td style={{ width:100 }}>{this.renderBusinessCenterDropDown(transaction)}</td>
                                                {this.props.options.showMovementCode ? <td style={{ width:30 }}>{transaction.movement_code}</td> : null}
                                                <td style={{ width:70 }}>{this.renderDocumentColumn(transaction)}</td>
                                                <td style={{ width:50 }}>{moment(transaction.date).format('DD/MM/YYYY')}</td>
                                                <td style={{ width:50 }}>{moment(transaction.created_dt).format('DD/MM/YYYY')}</td>
                                            </tr>
                                })
                            }
                        </tbody>
                    </Table>
                </CardBody>
            </Card>
        );
    }
    renderMobile = () => {
        return <Row>
            <Col xs="12" sm="12" md="12">
                {this.renderTitle()}
                <Card>
                    <CardHeader id="headingOne">
                        <Button block color="link" className="text-left m-0 p-0" onClick={() => this.setState({mobileFiltersOpen:!this.state.mobileFiltersOpen})} aria-expanded={this.state.mobileFiltersOpen} aria-controls="collapseOne">
                            <h5 className="m-0 p-0">Filtrar</h5>
                        </Button>
                    </CardHeader>
                    <Collapse isOpen={this.state.mobileFiltersOpen} data-parent="#accordion" id="collapseOne" aria-labelledby="headingOne">
                        <CardBody>
                            <Row>
                                <Col xs="6">
                                    <Label>Fecha de inicio</Label><DateTimePicker onChange={this.changeStartDt}/>
                                </Col>
                                <Col xs="6">
                                    <Label>Fecha de fin</Label><DateTimePicker onChange={this.changeEndDt}/>
                                </Col>
                            </Row>
                            <Row>
                                <Col xs="12">
                                    <Label>Categora</Label>
                                    <Input
                                        onChange={(event) => {this.onFilterBusinessCenter(event.target.value)}}
                                        type="select"
                                        name="select">
                                            <option key={'no-option'}>Seleccione una categora</option>
                                            {this.props.business_centers.map(bc => {
                                                return <option key={bc.id} value={bc.id}>{bc.name}</option>
                                            })}
                                    </Input>
                                </Col>
                            </Row>
                            <Row>
                                <Col xs="12">
                                    <button onClick={() => this.props.searchTransactions(this.state.startDt, this.state.endDt, this.state.business_center_id)} className="btn btn-primary pull-right">Filtrar</button>
                                </Col>
                            </Row>
                        </CardBody>
                    </Collapse>
                </Card>
            </Col>
            <Col xs="12" sm="12" md="12">
                {
                    this.props.transactions.map((transaction, index) => {
                        return <Card>
                                    <CardHeader>
                                        <strong>{util.formatMoney(transaction.amount)}</strong> el {moment(transaction.date).format('DD/MM/YYYY')}
                                        <Badge color={transaction.amount > 0 ? 'success' : 'warning'} className="float-right">{transaction.amount > 0 ? 'Ingreso' : 'Egreso'}</Badge>
                                    </CardHeader>
                                    <CardBody>
                                        <ListGroup role="tablist">
                                            <ListGroupItem>{this.renderBusinessCenterDropDown(transaction)}</ListGroupItem>
                                            <ListGroupItem>{transaction.description}</ListGroupItem>
                                            {this.renderCardDocuments(transaction)}
                                        </ListGroup>
                                    </CardBody>
                                </Card>
                    })
                }
            </Col>
        </Row>
    }
    render() {
        const {width} = this.state;
        const isMobile = width <= 500;

        return (
            <div>
                {this.props.children}
                <Modal isOpen={this.state.uploadAsset.open} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Adjuntar documento</ModalHeader>
                    <ModalBody>
                        <Row>
                            <input onChange={this.onChangeAssetDescription} value={this.state.uploadAsset.name} />
                        </Row>
                        {
                            IMAGE.indexOf(this.state.uploadAsset.type) >= 0
                            ?
                                <Row>
                                    <a href={this.state.uploadAsset.data} target='_blank'><img src={this.state.uploadAsset.data} height="200"/></a>
                                </Row>
                            :
                                <Row>
                                    <label>{this.state.uploadAsset.name}</label>
                                </Row>
                        }
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.uploadAsset}>Subir</Button>{' '}
                        <Button color="secondary" onClick={this.toggle}>Cancelar</Button>
                    </ModalFooter>
                </Modal>
                <Modal isOpen={this.state.detailAsset.open} toggle={this.toggleDetailModal}>
                    <ModalHeader toggle={this.toggleDetailModal}>Detalle documento</ModalHeader>
                    <ModalBody>
                        <Card>
                            <CardBody>
                                <Row>
                                    <label>{this.state.detailAsset.name}</label>
                                </Row>
                                {
                                    IMAGE.indexOf(this.state.detailAsset.type) >= 0
                                    ?
                                        <Row>
                                            <a href={this.state.detailAsset.url} target='_blank'><img src={this.state.detailAsset.url} height="200"/></a>
                                        </Row>
                                    :
                                        <Row>
                                            <a href={this.state.detailAsset.url} target='_blank'>{this.state.detailAsset.name}</a>
                                        </Row>
                                }
                            </CardBody>
                        </Card>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="danger" onClick={() => this.deleteAsset(this.state.detailAsset.transactionId ,this.state.detailAsset.id)}>Eliminar</Button>
                        <Button color="secondary" onClick={this.toggleDetailModal}>Cerrar</Button>
                    </ModalFooter>
                </Modal>
                {this.renderDocumentsModal()}
                {isMobile ? this.renderMobile() : this.renderDesktop()}
            </div>
        );
    }
}


